.nav-main{
    background-color: #535353;
    padding-bottom: 30px;
}
::placeholder{
    color: aqua;
    font-size: 15px;
    font-weight: 600;
}
.top{

    display: flex;
    justify-content: space-between;
    padding: 50px 70px;
}
input{
    width: 300px;
    height: 40px;
    background-color: transparent;
    color: white;
    border-radius: 5px;
    border: 1px solid white;
    padding: 5px 15px;
}
.buttons{
    display: flex;
    gap: 20px;
}

@media (max-width:700px){
    .top{
        /* justify-content: center; */
        padding: 30px 20px;
    }
    input{
        width: 30vw;
    }
}

@media (max-width:480px){
        input{
            width: 250px;
            
        }
    .top{
        justify-content: center;
        gap: 30px;
        align-items: center;
        flex-direction: column;
        padding: 30px 40px;
    }
}