.bgg{
    position:relative   
}
#imgbg{
    position:relative
}
.mid{
    position: absolute;
    min-height: 100vh;
    display: flex;
    background-image: url(../../images/bg.png);
    background-size: cover;
    padding: 50px 5px;
    justify-content: center;
    flex-wrap: wrap;
    gap: 20px;
}
.text{
    display: flex;
    flex-direction: column;
    gap: 10px;
    width: 60%;
}
.box{
    width: 30%;
    height: 30%;
    border-radius: 10px;
    backdrop-filter: blur(10px);
    border: 1px solid black;
    gap: 30px;
    border: .5px solid white;
    padding: 15px 20px;
    display: flex;
    background-color: rgba(255, 255, 255, 0.306);
    /* height: 300px; */
}
.foodimg{
    width: 40%;
    /* height: 100%; */
    border-radius: 50%;

    /* height: 100px; */
}
.imgs{
    width: 100%;
    height: 100%;

}
@media (max-width:1000px) {
    .mid{
        align-items: center;

        flex-direction: column;
    }
    .box{
        width: 70%;
    }
}

@media (max-width:700px){
    .box{
        width: 90%;
    }
}
